<template>
  <div class="home">
    <theader class="tHeader"></theader>
    <carousel class="carousel"></carousel>
    <div class="tabWraper">
      <div class="tabList">
        <a class="tabItem feature active">
          <p class="iconWraper"><i class="icon fa fa-heartbeat"></i></p>
          <p class="textWraper"><span class="text">精选</span></p>
        </a>
        <a class="tabItem select">
          <p class="iconWraper"><i class="icon fa fa-file-movie-o"></i></p>
          <p class="textWraper "><span class="text">选电影</span></p>
        </a>
        <a class="tabItem advance">
          <p class="iconWraper"><i class="icon fa fa-film"></i></p>
          <p class="textWraper"><span class="text">预告片</span></p>
        </a>
        <a class="tabItem review">
          <p class="iconWraper"><i class="icon fa fa-comment-o"></i></p>
          <p class="textWraper"><span class="text">影评</span></p>
        </a>
      </div>
    </div>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <feature class="swiper-slide"></feature>
        <select-movie class="swiper-slide"></select-movie>
        <advance class="swiper-slide"></advance>
        <review class="swiper-slide"></review>
      </div>
    </div>
  </div>

</template>

<script>
import theader from './theader'
import carousel from './carousel'

import feature from './feature/featureMainPage'
import review from './review/review'
import selectMovie from './selectMovie/selectMovie'
import advance from './advance/advance'
import Swiper from 'swiper'


export default {
  name: "home",
  components: {
    theader,
    carousel,
    feature,
    review,
    selectMovie,
    advance
  },
  methods: {
    tabView (el) {
      let tabItem = $('.tabList .tabItem')
      let len =tabItem.length
      for (let i=0; i < len; i++) {
        $(tabItem[i]).click( function () {
          el.slideTo(i)
        })
      }
    },
    changeTabColor () {
      let mySwiper = new Swiper('.swiper-container', {
        touchRatio: .6,
        on: {
          slideChangeTransitionStart: function () {
            let textItem = $('.tabList .tabItem')
            let activeIndex = this.activeIndex
            $(textItem[activeIndex]).addClass('active').siblings().removeClass('active')
          },
        }
      })
      this.tabView(mySwiper)
    }
  },
  mounted () {
    this.changeTabColor()
    let scrollTop
    let carouselHeight = $('.carousel').height()
    const tabWraper = $('.tabWraper')

    window.onscroll = function () {
      let theaderHeight =$('.tHeader').height()
      scrollTop = $(document).scrollTop();
      if (scrollTop > carouselHeight) {
        tabWraper.css({ 'position':'fixed', 'top':theaderHeight, 'z-index': 99})
      } else {
        tabWraper.css({ 'position':'static'})
      }
    }
  },
}
</script>

<style scoped lang="less">
  .home {
    background: aliceblue;
    width: 100%;
    height: 100%;
    position: ;
    .carousel {
      margin-top:calc(40px + 25*(100vw - 436px)/1100);
    }
    .tabWraper {
      width: 100%;
      margin-bottom: 2px;
      background: aliceblue;
      /*position: fixed;*/
      /*top: 55px;*/
      /*z-index: 99;*/
      .tabList {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        .tabItem.feature {
          color: #ff6600;
        }
        .tabItem.select {
          color: #ff1000;
        }
        .tabItem.advance {
          color: darkgoldenrod;
        }
        .tabItem.review {
          color: greenyellow;
        }
        .tabItem {
          /*color: #666;*/
          /*flex: 0 1 auto;*/
          flex: 0 1 auto;
          text-align: center;
          p {
            margin-top: 1.3rem;
          }
          span {
            font-size: 2rem;
          }
          i {
            font-size: 4rem;
          }
          p.iconWraper {
            padding-top: 2rem;
          }
          p.textWraper {
            padding-bottom: 2rem;
            color: #555;
          }
        }
        .tabItem.active {
          p.textWraper {
            color: inherit;
          }
        }
      }
    }
    .swiper-wrapper {
      /*width: 100%;*/
      .swiper-slide.active {
      }
    }
  }

</style>



