<template>
    <div class="onShow">
      <ul class="onShow_list">
        <li class="onShow_listItem" v-for="(item,index) in movieArr" :key="index">
          <div class="imgBox" v-bind:style="{backgroundImage:`url(${item.img})`,  backgroundPosition:`center center`, backgroundSize: `cover`, backgroundRepeat:'no-repeat'}">
          </div>
          <div class="messageBox">
            <div class="left">
              <p><span class="movieName">{{item.tCn}}</span></p>
              <p><span class="movieDesc"> {{ item.commonSpecial}}</span></p>
              <p><span class="movieActors"> {{ item.actors }}</span></p>
            </div>
            <div class="right">
              <p><span class="movieScore"> {{ item.ratingFinal}}</span></p>
              <p><a class="byTicket">购票</a></p>
            </div>
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
export default {
  name: "onShow",
  data () {
    return {
      movieArr: []
    }
  },
  methods: {
    getMovieList () {
      this.$http.get('/api/Showtime/LocationMovies.api', {
        params: {
          locationId: this.$store.state.locationId
        }
      }).then( (res) => {
        console.log(res)
        this.movieArr = res.data.ms
      } ).catch( (err) => {
          console.log(err);
      })
    }
  },
  created () {
    this.getMovieList()
  }

}
</script>

<style scoped lang="less">
  .onShow_list {
    padding: 0 .5rem;
    .onShow_listItem {
      padding: 1rem 0;
      height: 23rem;
      display: flex;
      border-bottom: 1px solid #ddd;
      .imgBox {
        flex: 1;
      }
      .messageBox {
        flex: 5;
        display: flex;
        padding-left: 1rem;
        font-size: calc(10px + 4*( 100vw - 436px)/1100);
        p {
          margin-bottom:calc(20px + 60*( 100vw - 436px)/1100);
          .movieName {
            font-size: calc(12px + 4*( 100vw - 436px)/1100);
          }
          .movieDesc {
            color: yellowgreen;
          }
          .movieActors {
            color: #555;
          }
        }
        p:first-child {
          margin-top: 1rem;
        }
        .left {
          flex: 6;
        }
        .right {
          flex: 1;
          .movieScore {
            color: yellowgreen;
            font-size: calc(12px + 4*(100vw - 436px)/1100);
          }
          .byTicket {
            color: #ff9900;
            padding: 2px 5px;
            display: inline-block;
            border: 1px solid #ff9900;
          }
        }
      }

    }
  }


</style>



