import Vue from 'vue'
import Router from 'vue-router'
// 首页
import thome from  '../components/home/home'

// 购票
import byTicket from '../components/byTicket/index'
import onShow from '../components/byTicket/onShow'
import aboutShow from '../components/byTicket/aboutShow'
import cinema from '../components/byTicket/cinemas'
// 我的
import mine from '../components/mine/index'
import movieDetails from '../components/home/movieDetails'

import login from '../components/mine/login'
import register from '../components/mine/register'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/thome'
    },
    {
      path: '/thome',
      name: 'thome',
      component: thome,
    },
    {
      path: '/byTicket',
      name: 'byTicket',
      redirect: '/byTicket/onShow',
      component: byTicket,
      children: [
        {
          path: '/byTicket/onShow',
          name: 'onShow',
          component: onShow
        },
        {
          path: '/byTicket/aboutShow',
          name: 'aboutShow',
          component: aboutShow
        },
        {
          path: '/byTicket/cinema',
          name: 'cinema',
          component: cinema
        }
      ]
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    },
    {
      path: '/movieDetails',
      name: 'movieDetails',
      component: movieDetails
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    }
  ]
})



